.page {
  // Базовые стили для всего сайта
  min-width: 1200px;
  margin: 0;
  font: 16px/24px "Roboto", "Arial", sans-serif;
  color: #283136;
  background-color: #ffffff;

  a {
    text-decoration: none;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  &__slider {
    margin-bottom: 80px;
  }
}
